@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.App {
  font-family: 'Roboto', sans-serif;
}

button {
  background: none;
  border: 1px solid grey;
  border-radius: 5px;
  text-transform: uppercase;
  font-size: 1.1em;
  padding: 5px 10px;
  letter-spacing: 1px;
}

h1 {
  display: block;
  padding: 20px auto;
  width: 100%;
  text-align: center;
}

main {
  width: calc(100% - 20px);
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  flex-direction: row;
}

.sidebar {
  min-width: 300px;
  width: 10%;
  margin-right: 5px;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.sidebar ul li {
  width: 100%;
  height: 100px;
  padding: 0;
  display: flex;

  align-items: center;
}

.sidebar ul li button {
  width: 100%;
  height: 90%;
  font-size: 2em;
  font-weight: bold;
}

.content {
  width: calc(90% - 5px);
}

.pix2pix {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto auto auto;
  grid-template-areas:
    'title  title'
    'canvas processed'
    'action action';
  grid-row-gap: 15px;
  row-gap: 15px;
}

.pix2pix-title {
  grid-area: title;
}

.pix2pix-canvas {
  grid-area: canvas;
}

.pix2pix-action {
  grid-area: action;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.pix2pix-action button {
  width: 200px;
  height: 100%;
  background-color: #2ea44f;
  color:white;
  height: 50px;
}

.pix2pix-processed {
  position: relative;
  grid-area: processed;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pix2pix-processed-info {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.7);
}

.pix2pix-processed img {
  border: 1px solid black;
  width: 512px;
  height: 512px;
}

canvas {
  border: 1px solid black;
  width: 512px;
  height: 512px;
}

.drawingcanvas {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.drawingcanvas button {
  margin-top: 10px;
}

