.pix2pix {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto auto auto;
  grid-template-areas:
    'title  title'
    'canvas processed'
    'action action';
  row-gap: 15px;
}

.pix2pix-title {
  grid-area: title;
}

.pix2pix-canvas {
  grid-area: canvas;
}

.pix2pix-action {
  grid-area: action;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.pix2pix-action button {
  width: 200px;
  height: 100%;
  background-color: #2ea44f;
  color:white;
  height: 50px;
}

.pix2pix-processed {
  position: relative;
  grid-area: processed;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pix2pix-processed-info {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.7);
}

.pix2pix-processed img {
  border: 1px solid black;
  width: 512px;
  height: 512px;
}
